<template>
  <main>
    <FinsetHeader></FinsetHeader>
    <div class="my-graph-wrap">
      <p class="title"></p>
      <div class="graph">
        <Gauge :value="gaugeValue" :text="gaugeText" />
      </div>
      <a @click="$router.push('/credit/main')"></a>
      <div class="info-wrap">
        <div class="left">
          <p class="key"> ()</p>
          <p class="value">{{ci_percentage}}<em>%</em></p>
        </div>
        <div class="right">
          <p class="key"></p>
          <p class="value">{{ci_rating_credit}}</p>
        </div>

      </div>
    </div>

    <div class="my-main-list">
      <!---->
      <div v-if="consumeSumAmt != null" class="list">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/consume/main')"></a>
            <p @click="$router.push('/consume/main')">{{ formatNumber(consumeSumAmt) }}<em></em></p>
          </div>
          <div class="right">
            <a @click="goMenu('csDetail')"></a>
            <p></p>
          </div>
        </div>
      </div>
      <div v-else class="list no">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/consume/main')"></a>
          </div>
          <div class="right">
            <a @click="$router.push('/scrap/ctrlFcLink')">  </a>
          </div>
        </div>
      </div>
      <!---->
      <div v-if="assetsSumAmt != null" class="list">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/assets/main')"></a>
            <p @click="$router.push('/assets/main')">{{ formatNumber(assetsSumAmt) }}<em></em></p>
          </div>
          <div class="right">
            <a @click="$router.push('/assets/dirInput')"></a>
            <p></p>
          </div>
        </div>
      </div>
      <div v-else class="list no">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/assets/main')"></a>
          </div>
          <div class="right">
            <a @click="$router.push('/scrap/ctrlFcLink')">  </a>
          </div>
        </div>
      </div>
      <!---->
      <div v-if="debtSumAmt!=null" class="list">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/debt/main')"></a>
            <p @click="$router.push('/debt/main')">{{formatNumber(debtSumAmt)}}<em></em></p>
          </div>
          <div class="right">
            <a @click="$router.push('/debt/register')"></a>
            <p></p>
          </div>
        </div>
      </div>
      <div v-else class="list no">
        <div class="item">
          <div class="left">
            <a @click="$router.push('/debt/main')"></a>
          </div>
          <div class="right">
            <a @click="$router.push('/debt/register')">-<em> </em></a>
          </div>
        </div>
      </div>
    </div>
    <div class="my-links">
      <div class="wrap">
        <a @click="$router.push('/credit/raiseMain')"><br></a>
        <a @click="$router.push('/credit/smartReport')"><br></a>
        <a @click="goMenu('share');"><br></a>
      </div>
    </div>
    <FinsetBottom></FinsetBottom>
  </main>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";

import ko from "vee-validate/dist/locale/ko.js";

import FinsetHeader from "./../common/FinsetHeader";
import FinsetBottom from "./../common/FinsetBottom";

import Gauge from "./../common/Gauge";

export default {
  name: "FinsetMain",
  data() {
    return {
      //
      creditInfo: "",
      ci_percentage: "", //%
      ci_rating_credit: "", //
      consumeSumAmt: "", //
      assetsSumAmt: "", //
      debtSumAmt: "", //
      gaugeValue: 0,
      gaugeText: ""
    };
  },
  components: {
    FinsetHeader: FinsetHeader,
    FinsetBottom: FinsetBottom,
    Gauge
  },
  // computed () {
  // },
  beforeCreate() {},
  created() {
    console.log("create start");
    this.$store.state.header.type = "main";
    this.$store.state.header.active = "main";

    //    
    if (this.$store.state.isScrap) {
      console.log("startScrapSt call");
      this.$parent.startAutoScrap();
      this.$store.state.isScrap = false;
    }

    this.getMainInfo();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    getMainInfo: function() {
      var _this = this;
      this.$http
        .get("/m/main/getMainInfo.json", {
          params: {}
        })
        .then(response => {
          var creditInfo = response.data.creditInfo;
          if (creditInfo != null) {
            _this.ci_percentage = creditInfo.percentage;
            _this.ci_rating_credit = creditInfo.rating_credit;
          }
          _this.creditInfo = creditInfo;
          _this.consumeSumAmt = response.data.consumeSumAmt;
          _this.assetsSumAmt = response.data.assetsSumAmt;
          _this.debtSumAmt = response.data.debtSumAmt;

          if (_this.creditInfo) {
            _this.gaugeValue = _this.creditInfo.rating_credit / 1000;
            _this.gaugeText = _this.creditInfo.grade_credit + "";
          }
          this.$store.state.isLoading = false;
        })
        .catch(e => {
          this.$toast.center(ko.messages.error);
        });
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    goMenu: function(menu) {
      if ("csDetail" == menu) {
        this.$router.push({
          path: "/consume/consumeDetail",
          query: { no_person: this.$store.state.user.noPerson }
        });
      } else if ("share" == menu) {
        this.$router.push({
          name: "shareMain",
          query: { cd_share: "02" }
        });
      }
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
</style>



