<template>
  <section>
    <div class="container">
      <div class="debt-regist">
        <p class="title"><em>  </em><br></p>
        <div class="list">
          <div>
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <multiselect :id="'cd_assets_class'" v-model="cd_assets_class" ref="cd_assets_class" placeholder="" :title="''" :options="assetsClassOptions" :onClose="selectAssetsClass" v-validate="'required'" data-vv-name=''>
                </multiselect>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>
          <div v-if="showKey=='30'||showKey=='50'||showKey=='60'">
            <ul class="flex">
              <li class="key">{{title_detail_class}}</li>
              <li class="value">
                <multiselect :id="'cd_detail_class'" v-model="cd_detail_class" ref="cd_detail_class" :placeholder="title_detail_class+''" :title="title_detail_class+''" :options="options_detail_class" v-validate="'required'" :data-vv-name='title_detail_class'>
                </multiselect>
              </li>
            </ul>
            <p class="warn" v-if="errors.has(title_detail_class)">{{errors.first(title_detail_class)}}</p>
          </div>

          <!---->
          <div v-if="showKey=='30'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="view_addr" readonly="readonly">
                <input type="hidden" v-model="real_estate_addr" v-validate="'required'" data-vv-name=''>
                <button class="search" @click="scAddress()"></button>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <div v-if="showKey=='40'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="nm_model" v-validate="'required|max:15'" data-vv-name=''>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <div v-if="showKey=='50'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="number" v-model="amount_jewelry" v-validate="'required|numeric|max:12'" data-vv-name=''>g
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <div v-if="showKey=='60'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="number" v-model="amt_balance" v-validate="'required|numeric|max:12'" data-vv-name=''>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <div v-if="showKey=='90'">
            <ul class="flex">
              <li class="key"></li>
              <li class="value">
                <input type="text" v-model="etc_assets" v-validate="'required|max:15'" data-vv-name=''>
              </li>
            </ul>
            <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
          </div>

          <!---->
          <template v-if="showKey!=''">
            <div>
              <ul class="flex">
                <!--/-->
                <li class="key">{{title_amt_evaluation}}</li>
                <li class="value">
                  <input type="number" v-model="amt_evaluation" v-validate="'required|numeric|max:12'" :data-vv-name='title_amt_evaluation'>
                </li>
              </ul>
              <p class="warn" v-if="errors.has(title_amt_evaluation)">{{errors.first(title_amt_evaluation)}}</p>
            </div>
            <div>
              <ul class="block">
                <li class="key"></li>
                <li class="value">
                  <input type="text" v-model="memo" v-validate="'max:15'" data-vv-name=''>
                </li>
              </ul>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p>
            </div>
          </template>
        </div>

      </div>
      <div v-if="isShowBtn" @click="createAssets()" class="btn-wrap float">
        <a class="solid blue box"></a>
      </div>
    </div>

    <vue-modal transitionName="zoom-in" name="postcode-modal">
      <div slot="body">
        <div class="v-modal__heading">
          <div class="v-modal__title"></div>
          <span @click="closePostcodeMd()" class="v-modal__close-btn">&times;</span>
        </div>
        <DaumPostcode :on-complete="selectPostcode" />
      </div>
    </vue-modal>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import ko from "vee-validate/dist/locale/ko.js";

import DaumPostcode from "vuejs-daum-postcode";

export default {
  name: "assetsDirInput",
  data() {
    return {
      assetsClassOptions: [
        { text: "", value: "30" },
        { text: "", value: "40" },
        { text: "", value: "50" },
        { text: "", value: "60" },
        { text: "", value: "90" }
      ],
      cd_assets_class: "",
      showKey: "",
      title_detail_class: "",
      options_detail_class: [],
      cd_detail_class: "",
      view_addr: "", // - (view)
      real_estate_addr: "", // - 
      nm_model: "", // - 
      amount_jewelry: "", // - 
      amt_balance: "", // - 
      etc_assets: "", // - 
      title_amt_evaluation: "",
      amt_evaluation: "", // - ,  - 
      memo: "", // - 
      isShowModal: false,
      isShowBtn: false
    };
  },
  components: {
    DaumPostcode
  },
  computed: {},
  watch: {
    isShowModal: function(param) {
      if (param) {
        this.$modals.show("postcode-modal");
      } else {
        this.$modals.hide("postcode-modal");
      }
    }
  },
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";
  },
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    //
    selectAssetsClass: function(param) {
      var _this = this;

      _this.cd_detail_class = "";
      _this.errors.clear();
      _this.initForm();

      //selectbox Setting
      if ("30" == param.value) {
        _this.title_detail_class = "";
        _this.options_detail_class = Common.makeOptions("cd_assets_prop", "");
      } else if ("50" == param.value) {
        _this.title_detail_class = "";
        _this.options_detail_class = Common.makeOptions("cd_assets_nbmt", "");
      } else if ("60" == param.value) {
        _this.title_detail_class = "";
        _this.options_detail_class = Common.makeOptions("cd_assets_frcr", "");
      } else {
        _this.title_detail_class = "";
        _this.options_detail_class = [];
      }

      if ("60" == param.value) {
        _this.title_amt_evaluation = "";
      } else {
        _this.title_amt_evaluation = "";
      }

      _this.showKey = param.value;
      _this.isShowBtn = true;
    },
    initForm: function() {
      var _this = this;
      _this.title_detail_class = "";
      _this.cd_detail_class = "";
      _this.view_addr = "";
      _this.real_estate_addr = "";
      _this.nm_model = "";
      _this.amount_jewelry = "";
      _this.amt_balance = "";
      _this.etc_assets = "";
      _this.title_amt_evaluation = "";
      _this.amt_evaluation = "";
      _this.memo = "";
    },
    //
    scAddress: function() {
      this.isShowModal = true;
    },
    //close
    closePostcodeMd: function() {
      this.isShowModal = false;
    },
    //   
    selectPostcode: function(param) {
      var _this = this;
      console.log(param.address);
      _this.view_addr = param.address;
      _this.real_estate_addr = param.address;
      this.isShowModal = false;
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    createAssets: function() {
      var _this = this;

      this.$validator.validateAll().then(res => {
        if (res) {
          Constant.options.title = "FINSET";
          this.$dialogs
            .confirm(" ?", Constant.options)
            .then(res => {
              if (res.ok) {
                var cd_assets_class = _this.cd_assets_class.value;
                var formData = new FormData();
                formData.append("cd_assets_class", cd_assets_class);
                if (
                  "30" == cd_assets_class ||
                  "50" == cd_assets_class ||
                  "60" == cd_assets_class
                ) {
                  formData.append(
                    "cd_detail_class",
                    _this.cd_detail_class.value
                  );
                }
                formData.append("real_estate_addr", _this.real_estate_addr);
                formData.append("nm_model", _this.nm_model);
                formData.append("amount_jewelry", _this.amount_jewelry);
                formData.append("amt_balance", _this.amt_balance);
                formData.append("etc_assets", _this.etc_assets);
                formData.append("amt_evaluation", _this.amt_evaluation);
                formData.append("memo", _this.memo);

                this.$http
                  .post("/m/assets/createAssetsInfo.json", formData)
                  .then(response => {
                    this.$toast.center(response.data.message);
                    if ("00" == response.data.result) {
                      _this.$router.push("/assets/main");
                    }
                  })
                  .catch(e => {
                    this.$toast.center(ko.messages.error);
                  });
              }
            });
        }
      });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



