<template>
  <section>
    <div class="container pb90">
      <div class="drop-top">  <br>    </div>
      <div class="checks">
        <input type="checkbox" id="check-all" @click="chkall" v-model="chkall"><label for="check-all" class="mt30"></label>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk1" disabled="true" v-model="chk1"><label for="chk1">       ,       . </label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk2" disabled="true" v-model="chk2"><label for="chk2">               .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk3" disabled="true" v-model="chk3"><label for="chk3">        .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk4" disabled="true" v-model="chk4"><label for="chk4">       5     .</label></p>
        </div>
        <div class="box-agree drop">
          <p><input type="checkbox" id="chk5" disabled="true" v-model="chk5"><label for="chk5">    .</label></p>
        </div>
      </div>
    </div>

    <div class="btn-wrap float">
      <a @click="quitChkYn" class="solid box blue"></a>
    </div>
  </section>
</template>

<script>
import Constant from "./../../assets/js/constant.js";
export default {
  name: "MypageDrop",
  data() {
    return {
      chk1: false,
      chk2: false,
      chk3: false,
      chk4: false,
      chk5: false,
      chkall: false,
      checked: ""
    };
  },
  components: {},
  computed: {},
  watch: {
    chkall: function() {
      if (this.chkall) {
        this.checked = "check";
        this.chk1 = true;
        this.chk2 = true;
        this.chk3 = true;
        this.chk4 = true;
        this.chk5 = true;
      } else {
        this.checked = "";
        this.chk1 = false;
        this.chk2 = false;
        this.chk3 = false;
        this.chk4 = false;
        this.chk5 = false;
      }
    }
  },
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = " ";
  },
  created() {},
  beforeMount() {},
  mounted() {
    $("#input").on("click", function(e) {
      e.preventDefault();
    });
  },
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    quitChkYn: function() {
      let _this = this;

      this.$dialogs
        .confirm("  ?", Constant.options)
        .then(res => {
          if (res.ok) {
            _this.dropPerson();
          } else {
            return false;
          }
        });
    },
    dropPerson: function() {
      let url = "/m/customercenter/customerQuitComp.json";
      let _this = this;

      var frm = new FormData();
      _this.$http
        .post(url, frm)
        .then(response => {
          if (response.data.result == "00") {
            //
            _this.$router.push("/mypage/dropDone");
          } else {
            _this.$toast.center(response.data.message);
          }
        })
        .catch(e => {
          _this.$toast.center(ko.messages.error);
        });
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



