<template>
  <section>
    <div v-if="isInit" class="con-top cut-top">
      <p><em>     </em><br><br>  </p>
      <a @click="openInfo()">?</a>
    </div>

    <div v-if="!isInit && debtList.length==0" class="con-top cut-top">
      <p><em>    .</em></p>
    </div>
    <div v-if="!isInit && debtList.length!=0" class="cut-result-top">
      <div v-for="debtInfo in debtList" :key="debtInfo.index" class="item">
        <div class="top">
          <p class="symbol"><img :src="debtInfo.fcImg" alt="" />{{debtInfo.nm_fc}}</p>
          <p class="date">{{formatDateDot(debtInfo.ymd_loan)}}</p>
        </div>
        <div class="goods-benefit">
          <div>{{debtInfo.ever_interest}}<em> %</em></div>
          <div>{{formatNumber(debtInfo.amt_contract)}}<em> </em></div>
        </div>
        <a v-if="'' != debtInfo.tel && debtInfo.tel != null" @click="callFc(debtInfo.tel)" class="btn"> </a>
        <div class="cate-tag">
          <em v-for="cutItem in debtInfo.cutItems" :key="cutItem.index">{{getCodeName(cutItem)}}</em>
        </div>
      </div>
    </div>

    <div class="accodion">
      <div class="info-massage">      .</div>
      <ul>
        <li>
          <div class="top"><span :class="{on: ynCredit}">   ?</span><em @click="closeItem('credit')"></em></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <datepicker v-model="creditFixDate" ref="creditFixOpen" :opend="Common.datepickerInit('div-date', this)" :language="ko" :format="formatDate" class="div-date"></datepicker>
                <button @click="openCreditFixPicker" class="cal"></button>
              </p>
            </div>
            <p class="text">  2              .</p>
          </div>
        </li>
        <li>
          <div class="top"><span :class="{on: ynTurnover}">    ?</span><em @click="closeItem('turnover')"></em></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <datepicker v-model="turnoverDate" ref="turnoverOpen" :opend="Common.datepickerInit('div-date', this)" :language="ko" :format="formatDate" class="div-date"></datepicker>
                <button @click="openTurnoverPicker" class="cal"></button>
              </p>
            </div>
            <!--
                      <div class="flex">
                          <p></p>
                          <p><button class="search"></button></p>
                      </div>
                      <div class="flex">
                          <p></p>
                          <p><button class="search"></button></p>
                      </div>
                      -->
            <!-- <p class="text">      .</p> -->
          </div>
        </li>
        <li>
          <div class="top"><span :class="{on: ynIncome}">   ?</span><em @click="closeItem('income')"></em></div>
          <div class="con">
            <!--
                      <div class="flex">
                          <p></p>
                          <p>
                              <select>
                                  <option>2018-08-01</option>
                              </select>
                          </p>
                      </div>
                      <div class="flex">
                          <p></p>
                          <p><input type="text"><em class="pl5"></em></p>
                      </div>
                      -->
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="income" v-validate="'required'" data-vv-name=''><em class="pl5"></em></p>
              <p class="warn" v-if="errors.has('')">{{errors.first('')}}</p> -->
              <p><input type="number" v-model="income" autocomplete="off"><em class="pl5"></em></p>
            </div>
            <p class="text">      15%     .</p>
          </div>
        </li>
        <li>
          <div class="top"><span :class="{on: ynDebt}">   ?</span><em @click="closeItem('debt')"></em></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <datepicker v-model="debtFixDate" ref="debtFixOpen" :opend="Common.datepickerInit('div-date', this)" :language="ko" :format="formatDate" class="div-date"></datepicker>
                <button @click="openDebtFixPicker" class="cal"></button>
              </p>
            </div>
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="debtBfAmt" v-validate="'required'" data-vv-name=' '><em class="pl5"></em></p>
              <p class="warn" v-if="errors.has(' ')">{{errors.first(' ')}}</p> -->
              <p><input type="number" v-model="debtBfAmt" autocomplete="off"><em class="pl5"></em></p>
            </div>
            <div class="flex">
              <p></p>
              <!-- <p><input type="number" v-model="debtAtAmt" v-validate="'required'" data-vv-name=' '><em class="pl5"></em></p>
              <p class="warn" v-if="errors.has(' ')">{{errors.first(' ')}}</p> -->
              <p><input type="number" v-model="debtAtAmt" autocomplete="off"><em class="pl5"></em></p>
            </div>
            <p class="text">        (15%)  .</p>
          </div>
        </li>
        <li>
          <div class="top"><span :class="{on: ynPos}">     ?</span><em @click="closeItem('pos')"></em></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <datepicker v-model="posFixDate" ref="posFixOpen" :opend="Common.datepickerInit('div-date', this)" :language="ko" :format="formatDate" class="div-date"></datepicker>
                <button @click="openPosFixPicker" class="cal"></button>
              </p>
            </div>
            <p class="text">      .</p>
          </div>
        </li>
        <li>
          <div class="top"><span :class="{on: ynCert}">   ?</span><em @click="closeItem('cert')"></em></div>
          <div class="con">
            <div class="flex">
              <p></p>
              <p>
                <datepicker v-model="certFixDate" ref="certFixOpen" :opend="Common.datepickerInit('div-date', this)" :language="ko" :format="formatDate" class="div-date"></datepicker>
                <button @click="openCertFixPicker" class="cal"></button>
              </p>
            </div>
            <div class="flex">
              <p></p>
              <p>
                <multiselect :id="'certification'" v-model="certification" :title="''" placeholder="" :options="certList" :onClose="onSelectCert">
                </multiselect>
              </p>
            </div>
            <p class="text">        .</p>
          </div>
        </li>
      </ul>
    </div>

    <div class="btn-wrap">
      <a @click="init()" class="stroke blue box"></a>
      <a @click="getReqIntrCut()" class="solid blue box mt10"></a>
    </div>

    <vue-modal transitionName="zoom-in" name="info-modal" v-on:popclose="closeInfo()">
      <ReqIntrCutInfo slot="body" v-on:popclose="closeInfo()"></ReqIntrCutInfo>
    </vue-modal>
  </section>
</template>

<script>
import Common from "./../../assets/js/common.js";
import Constant from "./../../assets/js/constant.js";
import korean from "vee-validate/dist/locale/ko.js";

import datepicker from "vuejs-datepicker";
import { ko } from "vuejs-datepicker/dist/locale";

import ReqIntrCutInfo from "./ReqIntrCutInfo.vue";

export default {
  name: "debtReqIntrCut",
  data() {
    return {
      Common: Common,
      ko: ko,
      isInit: true, // 
      currentDate: "", //
      ynCredit: false, // 
      ynTurnover: false, // 
      ynIncome: false, // 
      ynDebt: false, // 
      ynPos: false, // 
      ynCert: false, // 
      creditFixDate: "", // 
      turnoverDate: "", //
      income: "", //
      debtFixDate: "", // 
      debtBfAmt: "", //  
      debtAtAmt: "", //  
      posFixDate: "", // 
      certFixDate: "", // 
      certList: [
        { text: "", value: "" },
        { text: "", value: "01" },
        { text: "( )", value: "02" },
        { text: "", value: "03" },
        { text: "", value: "04" },
        { text: "", value: "05" },
        { text: "", value: "06" },
        { text: "", value: "07" },
        { text: "", value: "08" },
        { text: "", value: "09" },
        { text: "", value: "10" },
        { text: "", value: "11" },
        { text: "", value: "12" },
        { text: "", value: "13" },
        { text: "", value: "14" },
        { text: "", value: "15" }
      ], //list
      certification: "", //
      debtList: [] //list
    };
  },
  components: {
    datepicker: datepicker,
    ReqIntrCutInfo: ReqIntrCutInfo
  },
  watch: {
    //
    creditFixDate: function() {
      if ("" != this.creditFixDate) {
        this.ynCredit = true;
      } else {
        this.ynCredit = false;
      }
    },
    //
    turnoverDate: function() {
      if ("" != this.turnoverDate) {
        this.ynTurnover = true;
      } else {
        this.ynTurnover = false;
      }
    },
    //
    income: function() {
      if ("" != this.income) {
        this.ynIncome = true;
      } else {
        this.ynIncome = false;
      }
    },
    //
    debtFixDate: function() {
      if (
        "" != this.debtFixDate &&
        "" != this.debtBfAmt &&
        "" != this.debtAtAmt
      ) {
        this.ynDebt = true;
      } else {
        this.ynDebt = false;
      }
    },
    debtBfAmt: function() {
      if (
        "" != this.debtFixDate &&
        "" != this.debtBfAmt &&
        "" != this.debtAtAmt
      ) {
        this.ynDebt = true;
      } else {
        this.ynDebt = false;
      }
    },
    debtAtAmt: function() {
      if (
        "" != this.debtFixDate &&
        "" != this.debtBfAmt &&
        "" != this.debtAtAmt
      ) {
        this.ynDebt = true;
      } else {
        this.ynDebt = false;
      }
    },
    //
    posFixDate: function() {
      if ("" != this.posFixDate) {
        this.ynPos = true;
      } else {
        this.ynPos = false;
      }
    },
    //
    certFixDate: function() {
      if ("" != this.certFixDate && "" != this.certification.value) {
        this.ynCert = true;
      } else {
        this.ynCert = false;
      }
    },
    certification: function() {
      if ("" != this.certFixDate && "" != this.certification.value) {
        this.ynCert = true;
      } else {
        this.ynCert = false;
      }
    }
  },
  computed: {},
  beforeCreate() {
    this.$store.state.header.type = "sub";
    this.$store.state.title = "";
  },
  created() {
    this.setCurrentDate();
  },
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    // 
    setCurrentDate: function() {
      var _this = this;
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!
      var yyyy = today.getFullYear();

      if (dd < 10) {
        dd = "0" + dd;
      }

      if (mm < 10) {
        mm = "0" + mm;
      }

      _this.currentDate = yyyy + "-" + mm + "-" + dd;
    },
    //datepicker
    openCreditFixPicker: function() {
      this.$refs.creditFixOpen.showCalendar();
    },
    openTurnoverPicker: function() {
      this.$refs.turnoverOpen.showCalendar();
    },
    openDebtFixPicker: function() {
      this.$refs.debtFixOpen.showCalendar();
    },
    openPosFixPicker: function() {
      this.$refs.posFixOpen.showCalendar();
    },
    openCertFixPicker: function() {
      this.$refs.certFixOpen.showCalendar();
    },
    //multiselect
    onSelectCert: function(option) {
      var _this = this;
      _this.certification = option;
    },
    //list 
    getReqIntrCut: function() {
      var _this = this;
      var cutItems = [];
      if (!_this.validate()) return false;

      if (_this.ynCredit) cutItems.push("01");
      if (_this.ynTurnover) cutItems.push("02");
      if (_this.ynIncome) cutItems.push("03");
      if (_this.ynDebt) cutItems.push("04");
      if (_this.ynPos) cutItems.push("05");
      if (_this.ynCert) cutItems.push("06");

      console.log("cutItems" + cutItems);
      console.log("creditFixDate" + Common.formatDateDB(_this.creditFixDate));
      console.log("turnoverDate" + Common.formatDateDB(_this.turnoverDate));
      console.log("income" + _this.income);
      console.log("debtFixDate" + Common.formatDateDB(_this.debtFixDate));
      console.log("posFixDate" + Common.formatDateDB(_this.posFixDate));
      console.log("certFixDate" + Common.formatDateDB(_this.certFixDate));

      var formData = new FormData();
      formData.append("cutItems", cutItems);
      formData.append(
        "creditFixDate",
        Common.formatDateDB(_this.creditFixDate)
      );
      formData.append("turnoverDate", Common.formatDateDB(_this.turnoverDate));
      formData.append("income", _this.income);
      formData.append("debtFixDate", Common.formatDateDB(_this.debtFixDate));
      formData.append("posFixDate", Common.formatDateDB(_this.posFixDate));
      formData.append("certFixDate", Common.formatDateDB(_this.certFixDate));

      this.$http
        .post("/m/debt/listReqIntrCut.json", formData)
        .then(function(response) {
          //ICON 
          var list = response.data.debtList;
          for (var i = 0; i < list.length; i++) {
            list[i].fcImg =
              "/m/fincorp/getFinCorpIcon.crz?cd_fc=" + list[i].cd_fc;
          }
          _this.debtList = list;

          _this.isInit = false;

          console.log(_this.isInit);
          console.log(_this.debtList);
        })
        .catch(e => {
          _this.$toast.center(korean.messages.error);
        });
    },
    validate: function() {
      var _this = this;
      if (
        !_this.ynCredit &&
        !_this.ynTurnover &&
        !_this.ynIncome &&
        !_this.ynDebt &&
        !_this.ynPos &&
        !_this.ynCert
      ) {
        _this.$toast.center("  ");
        return false;
      }
      if (_this.ynDebt) {
        console.log(Number(_this.debtAtAmt));
        console.log(Number(_this.debtBfAmt));
        //
        if (Number(_this.debtBfAmt) * 0.85 < Number(_this.debtAtAmt)) {
          _this.$toast.center("    ");
          return false;
        }
      }
      if (_this.ynCert) {
        if ("" == _this.certification) {
          _this.$toast.center("  ");
          return false;
        }
      }

      return true;
    },
    formatNumber: function(data) {
      return Common.formatNumber(data);
    },
    formatDate: function(data) {
      return Common.formatDate(data);
    },
    formatDateDot: function(data) {
      return Common.formatDateDot(data);
    },
    getCodeName: function(data) {
      return Common.getCodeName("debt_cut_items", data);
    },
    //info
    openInfo: function() {
      var _this = this;
      _this.$modals.show("info-modal");
    },
    closeInfo: function() {
      var _this = this;
      _this.$modals.hide("info-modal");
    },
    // 
    callFc: function(tel) {
      console.log("tel" + tel);

      if (Constant.userAgent == "iOS") {
        Jockey.send("phoneCall", {
          phNum: tel
        });
      } else if (Constant.userAgent == "Android") {
        window.Android.phoneCall(tel);
      }
    },
    closeItem: function(item) {
      var _this = this;
      // var currentDate = _this.currentDate;

      if ("credit" == item) {
        _this.creditFixDate = "";
      } else if ("turnover" == item) {
        _this.turnoverDate = "";
      } else if ("income" == item) {
        _this.income = "";
      } else if ("debt" == item) {
        _this.debtFixDate = "";
        _this.debtBfAmt = "";
        _this.debtAtAmt = "";
      } else if ("pos" == item) {
        _this.posFixDate = "";
      } else if ("cert" == item) {
        _this.certFixDate = "";
        _this.certification = "";
      } else if ("init" == item) {
        //
        _this.creditFixDate = "";
        //
        _this.turnoverDate = "";
        //
        _this.income = "";
        //
        _this.debtFixDate = "";
        _this.debtBfAmt = "";
        _this.debtAtAmt = "";
        //
        _this.posFixDate = "";
        //
        _this.certFixDate = "";
        _this.certification = "";
      }
    },
    //
    init: function() {
      var _this = this;

      _this.isInit = true;
      $("li").removeClass("on");
      _this.closeItem("init");
    }
  }
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="scss">
</style>



